				url:'../share_merchandise/share_merchandise?tk='+tk+'&goods_id='+goods_id+'&pict_url='+pict_url
			})
			
		},
		// 跳转app链接
		clickwebview:function(){
			var that=this
			var urls=that.urls
			console.log(urls)
			uni.navigateTo({
				url:'../webview/webview?urls='+urls
			})
		},
		//  openTaobao(url){  
