		//         },  
		lunbanchange:function(e){
			// console.log(e)
			var cur=e.detail.current
			this.luncurrent=cur+1
		},
		clicklook:function(){
			var that=this 
			if(that.lookshow==true){
				that.isOpen=true
				that.lookshow=false
			}else{ 
				that.lookshow=true
				that.isOpen=false
			}
		},
		// 复制
		gocopy:function(e){
			console.log(e)
			var val=e.currentTarget.dataset.word
			// coputext.getText('ddddd')
			uni.setClipboardData({
				data:val ,
				success() {
					uni.showToast({
						title:'复制成功，打开淘宝即可',
						icon:'none'
					})
				}
			})  
		},
		// 返回上个页面
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
		// 返回顶部
		gotop:function(e){
			uni.pageScrollTo({
				scrollTop:0,
				duration:300
			})
		},
		togglePopup(type, open,text) {
			console.log(text)
			if(text =='addgo'){
				this.shoptext='加入购物车'
			}else{
				this.shoptext='确认'
			}
			console.log('text')
			this.type = type 
				this.$refs[open].open() 
		},
		change(e) {
			console.log(e.show)
		},
		cancel(type) { 
			this.$refs[type].close()
		},
		// 详情
		getGoodsInfo:function(){
			var that=this
			requestHttps({
				url:'/tb/goods/detail',
				method: 'POST',
				data: {
					goods_id:that.goods_id
				},
				success: res => {
					console.log(res) 
					that.islogin(res.data,2,that.goods_id)
					var data=res.data
					if(data.code==1){
						that.lodding=1
						that.goodinfor=data.data
						that.urls=data.data.coupon_share_url
						that.lkey=data.data.tkl_code
						that.banList=data.data.small_images
						that.imglist=data.data.item_description
						that.pict_url=data.data.pict_url
					}
				},
				fail: () => {},
				complete: () => {}
			})
		},
		// 猜你喜欢
		getlikeList:function(){
			var that=this
			if(that.flag){
				requestHttps({
					url:'/tb/guesslike/lists',
					method:'POST',
					data:{
						goods_id:that.goods_id,
						page:that.page
					},
					success: res=>{
						var restt=res
						 that.islogin(restt.data,2,that.goods_id)
						console.log(res)
						var data=res.data 
						var list=data.data
						if(data.code==1){
							
							if(that.page==1){
								that.looklist = list
							}else{
								that.looklist= that.looklist.concat(list)
							}
							if(list.length>=1){
								that.flag=true 
								that.liloading=true
							}else{
								that.liloading=true
								that.flag=false 
							}
						}
						
					}
				})
			}
		},
		// 到详情
		gopart:function(e){
			 var id=e.currentTarget.dataset.goods_id
			 uni.navigateTo({
				url: '../productdetail/productdetail?id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			 });
		},
		taps:function(e){
			console.log(e)
			var that=this
			that.currentIndex=e
			
		},
	},
	onLoad:function(options){
		console.log(options)
		var that=this
		var goods_id=options.id
		that.goods_id = goods_id
		that.getlikeList()
		uni.getSystemInfo({
			success:res=>{
				console.log(res)
				console.log('res.data')
				that.aheight=res.screenHeight
			}
		})
	},
	onShow:function(){
		var that=this
		that.getGoodsInfo()
	},
	onPageScroll(e) {
		// console.log(e)
		var that=this
		var scrollTop=e.scrollTop
		var stop=e.scrollTop
		if(scrollTop<=300){
			that.gohidden=true
		}else{
			that.gohidden=false
		}
		if(stop>=100){
			that.isClass=true
			that.showtop=false
		}else{
			that.isClass=false
			that.showtop=true
		}
		 
	},
	onReachBottom:function(){
		var that=this
		if(that.flag){
			var page=that.page
			page++
			that.page=page
			that.liloading=false
			that.getlikeList()
		}
		
	},
	 
}
